<template>
  <div>
    <ul class="blockImg"><li v-for="elem  in elems"><img  v-bind:src="elem.userImageURL || '/dist/logo.png'" alt="">
      <a v-bind:href="elem.pageURL" target="_blank">Смотреть</a>


    </li>    </ul>

  </div>
</template>
<script>

  export default {
    data()  {
      return {
        elems:[]


      }
    },

    created() {
      fetch('https://pixabay.com/api/videos?key=5574618-9f2251e0f5fdbede0f9cb5e69')
        .then(response => response.json())
        .then(json => {


          json.hits.forEach((item) => {
                this.elems.push(item)
              })
            console.log( this.elems)

          }

        )
    }
    ,
  }
</script>
<style lang="scss">
  .blockImg {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;

    li {
      list-style: none;
      width: 21%;
      margin-bottom: 1%;
    }

    img {
      height: 170px;
      width: 100%;
    }
  }

</style>



